import utils from '../../../common/js/util.js'
import neilModals from '@/components/neil-modals/neil-modals.vue';
export default {
	components: {
	    neilModals
	},
	data() {
		return {
			show1: false,
			show2: false,
			list:'',
		}
	},
	onLoad() {
		this.$store.dispatch('getServeInfo',{}).then(res=>{
			this.list = res.data
		}).catch(err=>{})
		// utils.Promisenet('v1/user-service/info').then(result=>{
		// 	this.list = result
		// })
	},
	methods: {
		bindClick(type) {			    
		    this[`show${type}`] = true				    
		},
		closeModal(type) {
		    this[`show${type}`] = false		
		},
		close(type) {
			this[`show${type}`] = false
		},
		
		tencent(){
			uni.navigateTo({
				url:'../../webview/webview?qq='+this.list.QQ
			})
		},
		wechat(){
			uni.downloadFile({
				url: this.list.wechat_images, 
				success: (res) => {
					if (res.statusCode === 200) {
						uni.saveImageToPhotosAlbum({
							filePath: res.tempFilePath,
							success:function(){
								uni.showToast({
									title: "保存成功",
									icon: "success"
								});
							},
							fail: function() {
								uni.showToast({
									title: "保存失败，请稍后重试",
									icon: "none"
								});
							}
						})
					}
				}
			})
		},
		phone(){
			uni.makePhoneCall({
				phoneNumber: `${this.list.service_tel}` //仅为示例
			});
		},
	}
}
